ect \ninformation about the activity of individual website users. For users who have opted in to \nreceive updates or other information from Ghent University via email, these reports may contain \npersonal information.\nMost Internet browsers automatically accept cookies, but you can set your browser to refuse \nthem or to alert you when they are being sent. You can access the information on the Ghent \nUniversity website without enabling cookies in your browser.\n"),_c('p'),_c('p',{staticClass:"font-weight-medium font-italic pb-0"},[_vm._v("Login/Passwords")]),_vm._v("\nSome sections of the Ghent University website may require login identifiers and/or passwords. \nYou are responsible for taking reasonable steps to ensure that no unauthorized person has access \nto your password or account login information. It is your sole responsibility to control the use of \nlogin information and passwords, to authorize, monitor and control access to and use of your website \naccount and password and to inform the website promptly of any need to deactivate a password.\n"),_c('p'),_c('p',{staticClass:"font-weight-medium font-italic pb-0"},[_vm._v("Third-Party Vendors/Content Providers")]),_vm._v("\nGhent University never shares any personal information you provide when requesting information with \nthird parties, except as required by law or in order to provide the services requested. Ghent \nUniversity occasionally will engage reputable third-party vendors to help us manage our website, \nprovide content and information, and interact better with our patients and visitors.\n"),_c('p'),_c('p',{staticClass:"font-weight-medium font-italic pb-0"},[_vm._v("Links to External Websites")]),_vm._v("\nThis Website Privacy Policy applies only to the Ghent University website. In order to provide \nwebsite users with other helpful information, the Ghent University website contains links to \nother websites. However, Ghent University provides no authority over external organizations’ \nwebsite